/*
    header.css
*/

.mobileHeading{
    background-color: #285172;
    color: white;
    font-family: 'Montserrat', sans-serif;
}

/* tablets*/
@media only screen and (min-width: 600px) {
    .footer{
        background-color: red;
    }

}

/* desktop */
@media only screen and (min-width: 768px) {

    .header{
        width: 100%;
        height: 112px;
        background-color: #285172;
        color: white;
        font-size: 25px;
        padding-top: 16px;
    }


}
@media only screen and (min-width: 1139px) {

    .header{
        width: 100%;
        height: 75px;
        background-color: #285172;
        color: white;
        font-size: 25px;
        padding-top: 16px;
    }
 

}
